
.type{
    position:absolute;
    color:black;
    z-index:99;
    background: rgb(192, 191, 191);
    text-transform:uppercase;
    padding:0px 10px;
    border-radius:5%;
}
.cus-card{
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background: transparent;
    color:rgba(223, 180, 128, 0.8);
    margin-top: 20px;
    border: 1px solid black;
    box-shadow: 0px 0px 8px 2px rgba(0,0,0,0.7);
    position:relative;
    z-index:1;    
    overflow: hidden;
    display: grid;
    justify-content: center;
    align-items: center;
    
    
    .card{
        
        height:100%;
        .card-img{
            width:100%;
            height:100%;
        }
        :hover{
            transition: 1s;
            transform:scale(1.05,1.05)
        }
    
    }
    
    .card-button{
        z-index:2;
        position: absolute;
        bottom:10%;
        left:50%;
        transform: translateX(-50%);
        button{
            color:black;
        }
    }
}
