.register{
    min-height:calc(100vh - 60px);
    // border: 1px solid black;
    .register-form{
        position: absolute;
        top:10%;
        left:50%;
        transform: translate(-50%,0);
        color:chocolate;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        max-width:900px;
        padding-top: 2rem;
        padding-bottom: calc( 2rem - 4px );
        overflow: hidden;
        .icon{
            display: inline-block;
            width:fit-content;
            position: relative;
            // height:3rem;
            left:-12px;
            // top:2%;
            // border:1px solid blue;
        }

        .padding{
            margin-top: 1rem;
            margin-bottom: 1rem;
        }   
    }
}